.main-container {
  max-width: 700px;
  margin: 0 auto;
  padding: 16px;
}

.main-title {
  margin-top: 16px;
  margin-bottom: 8px;
  text-align: center;
  font-size: 1.2rem;
}

.main-list {
  max-height: 220px;
  overflow-y: auto;
  margin-bottom: 16px;
  background: #f9f9f9;
  border-radius: 8px;
  padding: 8px;
}

.main-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.main-list li {
  margin-bottom: 8px;
}

.main-list a {
  color: #1976d2;
  font-weight: bold;
  text-decoration: none;
  word-break: break-word;
}

.main-buttons {
  display: flex;
  flex-direction: row;
  gap: 16px;
  margin-top: 24px;
}

.main-btn {
  flex: 1;
  padding: 12px 0;
  border-radius: 8px;
  border: none;
  background: #1976d2;
  color: #fff;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 2px 8px #0002;
  transition: background 0.2s;
}

.main-btn-green {
  background: #388e3c;
}

.video-item {
  position: relative;
  min-height: 110px;
  display: flex;
  align-items: flex-start;
  gap: 12px;
  background: #fff;
  border-radius: 8px;
  padding: 8px 8px 48px 8px; /* Más espacio abajo para los botones */
  box-shadow: 0 1px 4px #0001;
  flex-wrap: wrap;
}

.video-item .video-link {
  flex: 1 1 60%;
  display: flex;
  align-items: center;
  gap: 12px;
  min-width: 0;
}

.video-thumb {
  width: 80px;
  height: 60px;
  border-radius: 6px;
  object-fit: cover;
  flex-shrink: 0;
}

.video-title {
  font-size: 1em;
  font-weight: 500;
  color: #222;
  word-break: break-word;
}

.video-item-buttons {
  position: absolute;
  right: 8px;
  bottom: 8px;
  display: flex;
  gap: 6px;
  z-index: 2;
  background: transparent;
}

/* Botones favoritos y borrar */
.main-fav-btn,
.main-del-btn {
  padding: 4px 10px;
  border-radius: 6px;
  border: none;
  font-size: 0.95em;
  cursor: pointer;
  margin-top: 0;
  box-shadow: 0 1px 4px #0001;
}

.main-fav-btn {
  background: #ffd600;
  color: #333;
}

.main-del-btn {
  background: #e53935;
  color: #fff;
}

/* --- Responsive Design --- */
@media (max-width: 600px) {
  .main-container {
    padding: 4vw;
    max-width: 100vw;
  }

  .main-title {
    font-size: 1.05rem;
  }

  .main-list {
    max-height: 60vw;
    padding: 4vw 2vw;
  }

  .video-item {
    flex-direction: column;
    align-items: flex-start;
    min-height: 170px; /* Más espacio en móvil */
    padding-bottom: 0; /* Elimina el espacio extra abajo */
    padding-right: 0;
  }

  .video-item .video-link {
    flex-direction: row;
    align-items: flex-start;
    gap: 10px;
    width: 100%;
  }

  .video-thumb {
    width: 32vw;
    max-width: 120px;
    height: 24vw;
    max-height: 90px;
  }

  .video-title {
    font-size: 0.98em;
    max-width: 60vw;
    max-width: 100vw;
    white-space: normal;
  }

  .video-item-buttons {
    position: static;
    margin-top: 12px;
    width: 100%;
    justify-content: flex-end;
    gap: 10px;
  }
  .main-btn {
    font-size: 0.95em;
    padding: 10px 0;
  }
  .main-buttons {
    flex-direction: column;
    gap: 10px;
  }
}